// Кольори
$white: #ffffff;
$black: #000000;
$background-light-lemon: #fffdee;
$background-dark-lemon: #f0ecd6;
$accent-color: #f3c653;
$main-text-color: #5f5f5f;
$brown-text-color: #c79002;
$menu-text-color: #7a7a7a;




$text-color-for-dark-bg: #bababa;
$accent-color: #f3c653;
$outline: 1px solid tomato;
$dark-bg-section: rgba(0, 0, 0, 0.65);
$contact-text-color: #494848;

// ‌ Тінь на зображення
$picture-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
$picture-border: 1px solid #bababa;

// ‌ Шрифти
$times-font: 'TimesNewRoman', 'sans-serif';
$montserrat-font: 'Montserrat', 'sans-serif';
$film-font: 'FilmP3', 'sans-serif';

// Анімація
$main-transition-duration: 250ms;
$main-transtition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
